.Photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.Photo > img{
    padding: 20px;
    width: 300px;
    height: 300px;
}

.Statement {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    

}

.Statement > h4 {
    width: 600px;
    padding-bottom: 40px;
}

.PS {
    text-align: center;
}

.smithPhoto {
    width: 100%;
}

.mePhoto {
    border-radius: 50%;
}

.climbingPhoto {
    width: 400px;

}

.climbingBlurb {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.Resume {
    height: 100vh;

}

.WholeDoc {
    padding: 40px;
    font-family: 'Source Sans Pro', sans-serif;
}

span {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Date {
    color: #B35035;

}

.Info {
    color: grey;
    margin-top: 0px;
}

.Logos {
    width: 200px;
}

.SkillLogos {
   
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}

.SkillBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.ReleventProjects {
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ProjBox {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ProfessionalSummary {
    text-align: center;
    width: 600px;

}

.PSBox {
    display: flex;
    justify-content: center;
}

.Logo {
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: 200px; */
}

a {
    text-decoration: none;
    color: #B35035;
}

.PortLinkDiv {
    display: flex;
    justify-content: center;
}

.PortLink {
    font-weight: bolder;
    padding: 20px;
}


.PortPic {
    width: 600px;
}
.Contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.Contact > h1 {
    padding-top: 40px;
}

.CMLinks {
    display: flex;
    
    align-items: center;

    
}

.CMLinks > a {
    text-decoration: none;
    color: black;
}

.LinkLogos {
    width: 50px;
    padding: 20px;
}
